
import { defineComponent, computed, ref } from 'vue'
import { useMutation, useQuery, useResult } from '@vue/apollo-composable'
import { InstructorDetailQuery, InsertFavoriteInstructorMutation, DeleteFavoriteInstructorMutation } from '@/graphql/@types/types'
import instructorDetailQuery from '@/graphql/queries/instructorDetail'
import insertFavoriteInstructorMutation from '@/graphql/mutations/insertFavoriteInstructor'
import deleteFavoriteInstructorMutation from '@/graphql/mutations/deleteFavoriteInstructor'
import { useRouter } from 'vue-router'
import Header from '@/components/Header.vue'
import CourseItems from '@/components/CourseItems.vue'
import { faPlus, faCheck } from '@fortawesome/free-solid-svg-icons'
import { useStore } from 'vuex'

export default defineComponent({
  components: {
    Header,
    CourseItems
  },
  setup() {
    const store = useStore()
    const uid = store.state.currentUser?.uid
    const { currentRoute } = useRouter()

    const { result, onResult } = useQuery<InstructorDetailQuery>(instructorDetailQuery, {
      id: currentRoute.value.params.id,
      student_id: uid
    })
    const instructor = useResult(result, null, (data) => data.Instructors[0])
    const courses = useResult(result, null, (data) => data.Instructors[0].Courses)

    const { mutate: insertFavoriteInstructor, onDone: insertFavoriteInstructorDone } = useMutation<InsertFavoriteInstructorMutation>(
      insertFavoriteInstructorMutation,
      () => ({
        variables: {
          instructor_id: currentRoute.value.params.id,
          student_id: uid
        }
      })
    )
    const addFavoriteInstructor = async () => {
      await insertFavoriteInstructor()
    }

    const { mutate: deleteFavoriteInstructor, onDone: deleteFavoriteInstructorDone } = useMutation<DeleteFavoriteInstructorMutation>(
      deleteFavoriteInstructorMutation,
      () => ({
        variables: {
          instructor_id: currentRoute.value.params.id,
          student_id: uid
        }
      })
    )
    const deleteFavorite = async () => {
      await deleteFavoriteInstructor()
    }

    // お気に入り判定処理
    const isFavorite = ref<undefined | boolean>(undefined)
    const isFavoriteTeacher = useResult(result, null, (data) => data.Instructors[0].FavoriteInstructors.length)
    isFavorite.value = !!isFavoriteTeacher.value
    onResult(() => (isFavorite.value = !!isFavoriteTeacher.value))
    insertFavoriteInstructorDone(() => (isFavorite.value = true))
    deleteFavoriteInstructorDone(() => (isFavorite.value = false))

    const plusIcon = computed(() => faPlus)
    const checkIcon = computed(() => faCheck)

    return {
      plusIcon,
      checkIcon,
      instructor,
      courses,
      isFavorite,
      addFavoriteInstructor,
      deleteFavorite
    }
  }
})
