import { gql } from '@apollo/client/core'

const instructorDetailQuery = gql`
  query InstructorDetail($id: Int, $student_id: uuid) {
    Instructors(where: { id: { _eq: $id } }) {
      name
      title
      detail
      icon
      image
      Courses {
        id
        image
        title
        Instructor {
          name
        }
        Category {
          name
        }
      }
      FavoriteInstructors(where: { student_id: { _eq: $student_id } }) {
        id
      }
    }
  }
`
export default instructorDetailQuery
