import { gql } from '@apollo/client/core'

const deleteFavoriteInstructorMutation = gql`
  mutation DeleteFavoriteInstructor($instructor_id: Int, $student_id: uuid) {
    delete_FavoriteInstructors(where: { instructor_id: { _eq: $instructor_id }, student_id: { _eq: $student_id } }) {
      returning {
        id
      }
    }
  }
`

export default deleteFavoriteInstructorMutation
