import { gql } from '@apollo/client/core'

const insertFavoriteInstructorMutation = gql`
  mutation InsertFavoriteInstructor($instructor_id: Int, $student_id: uuid) {
    insert_FavoriteInstructors(objects: { instructor_id: $instructor_id, student_id: $student_id }) {
      returning {
        id
      }
    }
  }
`

export default insertFavoriteInstructorMutation
